.section {
  display: block;
  background-color: var(--default);

  &_indent-b {
    &_xxl {
      margin-bottom: var(--space-xxl);
    }

    &_xxxxl {
      margin-bottom: var(--space-xxxxl);
    }
  }

  &_space-v {
    &_xxl {
      padding-top: var(--space-xxl);
      padding-bottom: var(--space-xxl);
    }

    &_xxxxl {
      padding-top: var(--space-xxxxl);
      padding-bottom: var(--space-xxxxl);
    }
  }
}