.brand-logo {
  &_name {
    &_blue {
      background-color: #5dcdf9;
    }

    &_green {
      background-color: #23b324;
    }

    &_red {
      background-color: #ff3333;
    }

    &_purple {
      background-color: #c62cff;
    }
  }

  &_size {
    &_xxs {
      width: 24px;
      height: 24px;
    }

    &_xs {
      width: 32px;
      height: 32px;
    }
    
    &_s {
      width: 40px;
      height: 40px;
    }
    
    &_m {
      width: 60px;
      height: 60px;
    }
    
    &_l {
      width: 80px;
      height: 80px;
    }
  }
}